.OurClients-section {
  padding-top: 40px;
  padding-bottom: 40px;
}

.OurClients-section-title {
  color: #ffca2f;
  font-family: "Open Sans", sans-serif;
  font-weight: 700;
  font-size: 18px;
  text-align: center;
  letter-spacing: 2px;
  margin-top: 0;
  margin-bottom: 50px;
}
.client-category {
  display: flex;
  justify-content: space-between;
  position: relative;
  /* border-bottom : 1px solid rgba(0, 0, 0, .5); */
}

.client-category li {
  position: relative;
  margin-bottom: 30px;
  border-bottom: 2px solid rgba(0, 0, 0, 0.2);
  text-align: center;
  width: 100%;
  cursor: pointer;
}
.client-category li.active {
  border-bottom: 2px solid rgba(0, 0, 0, 1);
}
.client-category li.active ::afte {
  content: "";
  width: 100%;
  height: 2px;
  display: block;
  background-color: rgba(0, 0, 0, 0.7);
  position: absolute;
  bottom: 0;
}
.client-category-title {
  text-align: center;
  font-size: 20px;
}

.OurClient-list {
  display: flex;
  /* align-items: center; */
  justify-content: space-between;
  flex-wrap: wrap;
  row-gap: 15px;
  column-gap: 20px;
}

.OurClient-item {
  width: 170px;
  display: flex;
  flex-direction: column;
  gap: 15px;
  /* border: 1px solid #000; */
  /* box-shadow: 0px 5px 20px #000 ; */
}

.OurClient-item-img {
  width: 200px;
  height: 100px;
  object-fit: contain;
  box-shadow: 0px 10px 15px 3px rgba(0, 0, 0, 0.178);
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
}
.OurClient-item-img-cov {
  width: 200px;
  height: 80px;
  object-fit: fill;
}

.status_img {
  background-color: #cccccc;
}

