@media (max-width: 768px) {
  .client-category {
    flex-direction: column;
    position: relative;
    margin-bottom: 15px;
    padding-bottom: 0;
    border: 1px solid #000;
    justify-content: center;
    z-index: 1;
    padding: 1px 0;
  }

  .client-category.open {
    height: auto;
  }
  .client-category li {
    padding: 0;
    padding-left: 10px;
    background-color: #fff;
    position: absolute;
    margin: 0;
  }
  .client-category.open li {
    position: relative;
    height: 100%;
    border: none;
  }

  .client-category li.active {
    position: relative;
    z-index: 1;
    border: none;
    order: -1;
    margin-bottom: 0;
  }

  .client-category li.active::after {
    /* content: "";
    width: 0;
    height: 0;
    border: 40px solid transparent;
    border
    border-top:  solid #000 ;
    display: block;
    position: absolute; 
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
    transition: transform .3s; */
    content: "";
    width: 20px;
    height: 20px;
    display: block;
    background-image: url("../../assets/icons/arrow.svg");
    background-repeat: no-repeat;
    background-size: cover;
    position: absolute;
    right: 20px;
    top: 50%;
    transform: translateY(-50%);
  }
  /* .client-category.open li.active::after{
    transform: rotate(180deg);

  } */
  .client-category li h3 {
    text-align: start;
    height: 100%;
    margin: 0;
    padding: 10px 0;
  }
  .client-category li h3:hover {
    background: rgba(255, 255, 255, 0.2);
  }
}

@media (max-width: 560px) {
  .client-category {
    border-radius: 12px;
    padding: 7px;
    box-shadow: 0px 10px 15px 3px rgba(0, 0, 0, 0.178);
    border: none;
  }
  .client-category li {
    border: none;
    padding-left: 5px;
    width: 98%;
  }
  .client-category h3 {
    width: 50%;
  }

