.AboutUs-section {
  padding-top: 50px;
  padding-bottom: 50px;
  background-image: url(../../assets/images/background-about.jpg);
  background-position: right;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: rgba(0, 0, 0, 0.486);
  background-blend-mode: color;
  width: 100%;
}

.AboutUs-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  gap: 10px;
}

.AboutUs-box {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 10px;
  flex-wrap: wrap;
}

.AboutUs-img-box-girl {
  width: 200px;
  border: 1px solid #fff;
  border-radius: 10px;
  background-color: #003f34;
}


.AboutUs-user-title {
  margin: 0;
  padding: 0px 5px 5px 5px;
  color: #ffca2f;
  text-align: center;
  letter-spacing: 1px;
}

.AboutUs-user-subtitle {
  margin: 0;
  padding: 0px 0px 5px 0px;
  color: #fff;
  text-align: center;
}

.AboutUs-img-box {
  width: 200px;
  border: 1px solid #fff;
  border-radius: 10px;
  background-color: #003f34;
}

.AboutUs-img {
  width: 100%;
  height: 300px;
  border-radius: 10px;
  object-fit: cover;
}

.AboutUs-section-desc {
  width: 100%;
  max-width: 400px;
  line-height: 1.5em;
  font-size: 18px;
  color: #fff;
}

.AboutUs-section-title {
  line-height: 1.1em;
  font-size: 25px;
  margin-top: 0;
  margin-bottom: 20px;
  color: #ffca2f;
  text-align: center;
}

.AboutUs-section-subtitle {
  width: 100%;
  max-width: 500px;
  line-height: 1.2em;
  font-size: 40px;
  margin-top: 0;
  color: #fff;
}
@media (min-width: 760px) {
  .AboutUs-user-title {
    font-size: 16px;
  }
}
@media (max-width: 560px) {
  .AboutUs-user-title {
    font-size: 15px;
  }
}

@media only screen and (max-width: 450px) {
  .AboutUs-img-box {
    width: 270px;
    border: 1px solid #fff;
    border-radius: 10px;
    background-color: #003f34;
  }

  .AboutUs-img-box-girl {
    width: 270px;
    border: 1px solid #fff;
    border-radius: 10px;
    background-color: #003f34;
  }

  .AboutUs-img {
    width: 100%;
    height: 400px;
    border-radius: 10px;
    object-fit: cover;
  }
}
